.fontcolor {
  color: #c69c6d !important; }

:global(.scroll_header) {
  background-color: black !important;
  box-shadow: black 0.5px 0.5px 5px; }

:global(.header) {
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999; }
  :global(.header) .logo {
    height: 80px;
    position: relative; }
    :global(.header) .logo a {
      height: 85%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
      :global(.header) .logo a img {
        height: 100%; }
  :global(.header) .hamburger {
    width: 40px;
    height: 40px;
    top: 15px;
    left: 15px;
    float: left;
    background-color: #C69c6d;
    border-radius: 5px;
    position: fixed;
    z-index: 999; }
  :global(.header) .hamburger:before {
    content: '';
    width: 60%;
    height: 2px;
    background-color: #fff;
    border-radius: 5px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    margin: auto;
    box-shadow: 0px 5px 0px #fff, 0px -5px 0px #fff; }
  :global(.header) .navbar {
    background-color: rgba(0, 0, 0, 0.8);
    width: 300px;
    min-height: 100vh; }
  :global(.header) .nav_title {
    color: #ffffff; }
    :global(.header) .nav_title a {
      color: #ffffff; }
    :global(.header) .nav_title .nav_font {
      font-size: 24px;
      padding: 15px 0;
      margin-left: 5px;
      font-family: KangXiZiDianTi;
      cursor: pointer; }
    :global(.header) .nav_title .item_list a {
      padding: 15px 0;
      padding-left: 40px;
      display: block; }

@media screen and (min-width: 1200px) {
  :global(.header) {
    display: flex;
    justify-content: space-between; }
    :global(.header) .logo a {
      left: 0%;
      transform: translateX(50%);
      transform: translateY(-50%);
      left: 20px; }
    :global(.header) .hamburger {
      display: none; }
    :global(.header) .navbar {
      display: flex;
      justify-content: flex-end;
      background-color: unset;
      width: 100%;
      min-height: unset; }
    :global(.header) .nav_title {
      display: flex;
      color: #ffffff; }
      :global(.header) .nav_title .nav_font {
        width: 180px;
        font-size: 24px;
        padding: 30px 0 0 0;
        margin: 0;
        text-align: center; }
      :global(.header) .nav_title .item_list {
        margin-top: 7px;
        background-color: rgba(0, 0, 0, 0.8); }
        :global(.header) .nav_title .item_list a {
          padding-left: 0px; }
      :global(.header) .nav_title .nav_engfont {
        display: block;
        margin-top: 5px;
        font-size: 14px; } }
